*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
}

body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
}

input, button, textarea, select {
    font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
}

#root, #__next {
    isolation: isolate;
}

/* COMMON STYLING */
:root {
    --black: rgb(25, 25, 25);
    --light-black: rgb(78, 78, 78);
    --grey: rgb(32, 32, 32);
    --light-grey: rgb(146, 146, 146);
    --grey-border: rgba(146, 146, 146, 0.2);
    --button-hover: rgba(255, 255, 255, 0.055);
    --create-task-svg: #ff1700;
    --create-task-svg-hover: #dc4c3e;
    --add-btn-disabled: #eda59e;
    --high-priority: #d1453b;
    --high-priority-background: #d1453b2c;
    --high-priority-hover: #d1453b6e;
    --medium-priority: #eb8909;
    --medium-priority-background: #eb890942;
    --medium-priority-hover: #eb890985;
}

body {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
}

.app {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr 6fr;
}

/* SIDEBAR */
.sidebar {
    background-color: var(--grey);
    color: var(--light-grey);
    padding: 16px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    font-size: 0.95rem;
}

.logo img {
    width: 80px;
    height: auto;
}

.projects-header {
    display: flex;
    justify-content: space-between;
    font-weight: 500;
}

.projects-header, .projects-container > div {
    cursor: pointer;
    padding: 4px 8px;
    border-radius: 6px;
    transition: background-color 20ms ease-in;
}

.projects-header:hover, .projects-container > div:hover, .add-btn:hover, .delete-btn:hover {
    background-color: var(--button-hover);
}

.project-item {
    display: flex;
    justify-content: space-between;
}

.add-btn, .delete-btn {
    width: 24px;
    height: auto;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 20ms ease-in;
    color: red;
}

.btn-container, .project-item > img {
    opacity: 0;
    visibility: hidden;
}

.projects-header:hover .btn-container, .project-item:hover > img {
    opacity: 1;
    visibility: visible;
}

/* MAIN CONTENT */
.content {
    background-color: var(--black);
    overflow-y: auto;
}

.project-content {
    color: var(--light-grey);
    margin-top: 44px;
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    gap: 12px;
}

.project-content > h1 {
    border: 1px solid transparent;
}

.project-content > h1:hover {
    border-color: var(--grey-border);
}

.project-content > h1, .project-name-input {
    grid-column: 2 / 3;
}

.project-name-input {
    color: var(--light-grey);
    padding: 0;
    font-size: 2rem;
    font-weight: bold;
    background: none;
    border: none;
}

.project-name-input:focus {
    outline: none;
}

.create-task-btn {
    color: #808080;
    grid-column: 2 / 3;
    border: none;
    background: none;
    display: flex;
    gap: 4px;
    cursor: pointer;
    align-items: center;
    padding: 8px;
    border-top: 1px solid var(--grey-border);
}

.create-task-btn > svg {
    color: var(--create-task-svg);
}

.create-task-btn:hover {
    color: var(--create-task-svg-hover);
}

.create-task-btn:hover > svg {
    background-color: var(--create-task-svg-hover);
    border-radius: 50%;
    color: #ffffff;
}

/* FORM */
.todo-form {
    grid-column: 2 / 3;
    display: flex;
    flex-direction: column;
    border: 1px solid #808080;
    border-radius: 8px;
    background-color: #252525;
}

.input-container {
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.input-container > input, .input-container > textarea {
    border: none;
    background: none;
    color: var(--light-grey);
}

.input-container > input:focus-visible, .input-container > textarea:focus-visible {
    outline: none;
}

.sec-input-container {
    margin-top: 12px;
    display: flex;
    gap: 12px;
}

.sec-input-container > div {
    display: flex;
    gap: 4px;
}

.sec-input-container input, .sec-input-container select {
    border: 1px solid #808080;
    border-radius: 4px;
    background: none;
    color: var(--light-grey);
}

.sec-input-container .high {
    color: var(--high-priority);
}

.sec-input-container .medium {
    color: var(--medium-priority);
}

.form-btn-container {
    display: flex;
    justify-content: end;
    gap: 12px;
    border-top: 1px solid #808080;
    padding: 12px;
    font-weight: 600;
    font-size: 0.85rem;
}

.cancel-btn {
    background-color: #f5f5f5;
}

.cancel-btn:hover {
    background-color: #e5e5e5;
}

.add-task-btn {
    background-color: var(--create-task-svg-hover);
    color: #ffffff;
}

.add-task-btn:hover {
    background-color: #df2f1f;
}

.add-task-btn:disabled {
    background-color: var(--add-btn-disabled);
    cursor: auto;
}

.cancel-btn, .add-task-btn {
    cursor: pointer;
    border: 1px solid transparent;
    border-radius: 4px;
    padding: 4px 12px;
}

/* Task Section */
.todo-container {
    grid-row: 3 / 4;
    grid-column: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.todo {
    display: flex;
    align-items: center;
    gap: 16px;
    border: 1px solid transparent;
    border-radius: 12px;
    padding: 12px;
}

.todo:hover {
    border-color: var(--grey-border);
}

.todo:hover > .edit-btn {
    opacity: 1;
    visibility: visible;
}

.todo-content {
    border-left: 1px solid var(--grey-border);
    padding-left: 12px;
}

.due-date {
    margin-top: 12px;
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    gap: 4px;
}

.date-icon {
    width: 24px;
    height: auto;
    color: var(--light-grey);
}

.edit-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4px;
    width: 28px;
    height: 28px;
    background: none;
    border: none;
    color: var(--light-grey);
    align-self: flex-start;
    margin-left: auto;
    cursor: pointer;
    border-radius: 6px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.2s ease, visibility 0.2s ease;
}

.edit-btn:hover {
    background-color: rgba(255, 255, 255, 0.055);
}

/* Base styles for checkbox */
.todo-button {
    padding: 0;
    border: none;
    background: none;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.todo-button > span {
    width: 100%;
    height: 100%;
    border: 2px solid var(--light-grey);
    border-radius: 50%;
    background-color: var(--black);
}

.todo-button:hover > span {
    background-color: var(--light-black);
}

.todo-button path {
    stroke: rgb(214, 214, 214);
}

/* Priority-specific styles */
.high-priority > span {
    border-color: var(--high-priority);
    background-color: var(--high-priority-background);
}

.high-priority path {
    stroke: var(--high-priority)
}

.high-priority:hover > span {
    background-color: var(--high-priority-hover);
}

.medium-priority > span {
    border-color: var(--medium-priority);
    background-color: var(--medium-priority-background);
}

.medium-priority path {
    stroke: var(--medium-priority);
}

.medium-priority:hover > span {
    background-color: var(--medium-priority-hover);
}
